.kbcki-two-sided-bg-container__content {
  max-width: 1200px;
  margin: auto;
  position: relative;
  z-index: 1; }

.kbcki-two-sided-bg-container__bg-pane {
  position: fixed;
  top: 0;
  width: 50vw;
  height: 100vh; }
  @media (min-width: 900px) {
    .kbcki-two-sided-bg-container__bg-pane--left {
      left: 0;
      background: #797979; }
    .kbcki-two-sided-bg-container__bg-pane--right {
      right: 0;
      background: #FBFBFB; } }
  @media (min-width: 600px) and (max-width: 900px) {
    .kbcki-two-sided-bg-container__bg-pane--left {
      left: 0;
      width: 0; }
    .kbcki-two-sided-bg-container__bg-pane--right {
      right: 0;
      width: 100vw;
      background: #FBFBFB; } }
  @media (max-width: 600px) {
    .kbcki-two-sided-bg-container__bg-pane--left {
      left: 0;
      width: 0; }
    .kbcki-two-sided-bg-container__bg-pane--right {
      right: 0;
      width: 100vw;
      background: #FBFBFB; } }
