.kbcki-container {
  background: #FBFBFB;
  min-height: 100vh;
  display: flex; }
  @media (min-width: 900px) {
    .kbcki-container {
      flex-direction: row; } }
  @media (min-width: 600px) and (max-width: 900px) {
    .kbcki-container {
      flex-direction: column;
      margin-top: 48px; } }
  @media (max-width: 600px) {
    .kbcki-container {
      flex-direction: column;
      margin-top: 36px; } }
